body::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-thumb {
    background: #737373;
    border-radius: 20px;
}

section {
    height: calc(100vh - 65px);

    

    /* display: flex;
    justify-content: center;
    align-items: center; */

    /* font-weight: bold; */
}


nav{
    display: flex;
    padding: 0% 6%;
    justify-content: space-between;
    align-items: center;
    height: 80px;
}   


nav img{
    width: 100px;
}

.nav-links{
    flex: 1 1;
    text-align: right;
}

.nav-links ul li{
    list-style: none;
    display: inline-block;
    padding: 8px 12px;
    position: relative;
}

.nav-links ul li a {
    color: black;
    text-decoration: none;
    display: inline !important;
    margin: 10px;
    font-size: 1.5rem; 
    font-weight: bolder;
}

.nav-link{
    color: black;
}

.nav-links ul li a:hover{
    color: darkblue;
}

.active {
    color: darkblue !important;
    border-bottom: 3px solid darkblue;
    transition: 0.3s;
}


nav .fa{
    display: none;
}


@media(max-width: 1115px){
    .nav-links{
        position: absolute;
        background: #26272b;
        height: 60vh;
        top: 0;
        right: 0;
        text-align: right;
        z-index: 2;
        /* padding: auto; */
        border-radius: 20% 0 0 100%;
    }

    .menu-open{
        width: 250px;
        transition: 0.3s;
    }
    
    .menu-close{
        width: 0px;
        display: none;
        transition: 0.3s;
    }

    .nav-links ul{
        padding: none !important;
    }
    .nav-links ul li{
        padding: 20px 0;
    }
    .nav-link{
        color: white !important;
    }
    .active{
        color: darkblue !important;
    }

    nav .fa{
        display: block;
        margin: 10px;
        cursor: pointer;
    }
    nav .fa-bars{
        font-size: 22px;
        color: black;
    }
    nav .fa-times{
        position: absolute;
        top: 2%;
        left: 5%;
        font-size: 30px;
        color:aliceblue;
    }
}
.site-footer {
    background-color: #26272b;
    padding: 45px 0 20px;
    font-size: 15px;
    line-height: 24px;
    color: #737373;
}

.site-footer hr {
    border-top-color: #bbb;
    opacity: 0.5
}

.site-footer hr.small {
    margin: 20px 0
}

.site-footer h6 {
    color: #fff;
    font-size: 16px;
    text-transform: uppercase;
    margin-top: 5px;
    letter-spacing: 2px
}

.site-footer a {
    color: #737373;
}

.site-footer a:hover {
    color: #3366cc;
    text-decoration: none;
}

.footer-links {
    padding-left: 0;
    list-style: none
}

.footer-links li {
    display: block
}

.footer-links a {
    color: #737373
}

.footer-links a:active,
.footer-links a:focus,
.footer-links a:hover {
    color: #3366cc;
    text-decoration: none;
}

.footer-links.inline li {
    display: inline-block
}

.site-footer .social-icons {
    text-align: right
}

.site-footer .social-icons a {
    width: 40px;
    height: 40px;
    line-height: 40px;
    margin-left: 6px;
    margin-right: 0;
    border-radius: 100%;
    background-color: #33353d
}

.copyright-text {
    margin: 0
}

@media (max-width:991px) {
    .site-footer [class^=col-] {
        margin-bottom: 30px
    }
}

@media (max-width:767px) {
    .site-footer {
        padding-bottom: 0
    }

    .site-footer .copyright-text,
    .site-footer .social-icons {
        text-align: center
    }
}

.social-icons {
    padding-left: 0;
    margin-bottom: 0;
    list-style: none
}

.social-icons li {
    display: inline-block;
    margin-bottom: 4px
}

.social-icons li.title {
    margin-right: 15px;
    text-transform: uppercase;
    color: #96a2b2;
    font-weight: 700;
    font-size: 13px
}

.social-icons a {
    background-color: #eceeef;
    color: #818a91;
    font-size: 16px;
    display: inline-block;
    line-height: 44px;
    width: 44px;
    height: 44px;
    text-align: center;
    margin-right: 8px;
    border-radius: 100%;
    transition: all .2s linear
}

.social-icons a:active,
.social-icons a:focus,
.social-icons a:hover {
    color: #fff;
    background-color: #29aafe
}

.social-icons.size-sm a {
    line-height: 34px;
    height: 34px;
    width: 34px;
    font-size: 14px
}

.social-icons a.facebook:hover {
    background-color: #3b5998
}

.social-icons a.twitter:hover {
    background-color: #00aced
}

.social-icons a.github:hover {
    background-color: #010409
}

.social-icons a.linkedin:hover {
    background-color: #007bb6
}

.social-icons a.hashnode:hover {
    background-color: #2962ff
}

@media (max-width:767px) {
    .social-icons li.title {
        display: block;
        margin-right: 0;
        font-weight: 600
    }
}
.profile{
    width: calc(20vw + 100px);
    border-radius: 50%;
    position: absolute;
    top: 30%;
    right: 15%;
    border: 5px solid darkblue;
    transition: 0.3s;
}

.profile:hover{
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
    transition: 0.3s;
}

.bio{
    position: absolute;
    left: 15%;
}
.b0{
    font-size: 5rem;
    top: 40%;
}

.b1{
    font-size: 5rem;
    top: 50%;
}
.b2{
    top: 65%;
}

@media(max-width: 900px) {
    .profile{
        right: 5%;
    }
    .bio{
        position: absolute;
        left: 15%;
    }
    .b0{
        font-size: 3rem;
        top: 50vw;
    }
    
    .b1{
        font-size: 3rem;
        top: 60vw;
    }
    .b2{
        top: 70vw;
    }
}

@media(max-width: 450px) {

    .profile{
        position: static;
        width: 60vw;
        margin: 20vw;
        border-radius: 50%;
        top: 30%;
        right: 15%;
        border: 2px solid darkblue;
        transition: 0.3s;
    }
    .bio{
        position: static;
        width: 70vw;
        margin-left: 15vw;
        left: 15%;
    }
    .b0{
        font-size: 3rem;
        bottom: 20vw;
    }
    
    .b1{
        font-size: 3rem;
        top: 60vw;
    }
    .b2{
        top: 70vw;
    }
}

